<template>
  <section v-if="lightboxVisibility" class="lightBox">
      <div @click="closeIfNotImage" @keyup.esc="closeLightbox" class="lightbox">
          <div class="container">
            <img :src="imageSrc.length > 1 ? imageSrc : src" alt="" srcset="">
          </div>
          <button @click="closeLightbox" class="close-button">&times;</button>
      </div>
  </section>
</template>

<script>
export default {
    data(){
        return{
            lightboxVisibility : false,
            imageIndex: 0,
            imageSrc : ''
        }
    },
    props:{
        src : String,
        fullData : Array,
        visible: Boolean,
        index: Number
    },
    computed:{
        lightboxIndex:{
            get(){
                return this.index;
            },
            set(){
                this.imageIndex;
            }
        },
        lightboxSource:{
            get(){
                return this.src;
            },
            set(){
                this.imageSrc
            }
        }
    },
    methods:{
        closeLightbox(){
            this.lightboxVisibility = false;
            this.$emit('visibilityChanged', this.lightboxVisibility);
            this.imageSrc = "";
        },
        closeIfNotImage(e){
            e.target.tagName != 'IMG' ? this.closeLightbox() : null;
        }
    },
    created() {
    document.onkeydown = evt => {
      evt = evt || window.event;
      if (evt.keyCode == 27) {
        this.closeLightbox();
      }
      else if(evt.keyCode == 39){
         this.imageIndex < (this.fullData.length -1) ? this.imageIndex++ : this.imageIndex;
         this.imageSrc = this.fullData[this.imageIndex];
      }
      else if(evt.keyCode == 37){
         this.imageIndex > 0 ? this.imageIndex-- : null;
         this.imageSrc = this.fullData[this.imageIndex];
      }
    };
  },
  watch:{
    visible: function(prev,next){
        this.lightboxVisibility = prev;
    }
  }
}
</script>

<style lang="scss">
    .lightbox{
        width: 100vw;
        height:100vh;
        position: fixed;
        top: 0;
        left: 0;
        right:0;
        bottom:0;
        background-color: rgba(0,0,0,0.91337);
       
        z-index: 10;
        display: flex;
        align-items: center;
        justify-content: center;
        .close-button{
            color: white;
            position: absolute;
            top: 0px;
            z-index: 11;
            font-size: 45px;
            right: 50px;
            cursor: pointer;
            padding: 25px;
        }
        .container{
            display: flex;
            justify-content: center;
            align-items: center;
        }
    }
</style>